$weezer-color: #189BCC;
$brat-color: #89CC04;

* {
  margin: 0 auto;
  padding: 0;
}

@font-face {
  font-family: 'brat';
  src: url(/build/fonts/arialnarrow.ttf);
}

@font-face {
  font-family: 'weezered';
  src: url(/build/fonts/weezerfont.ttf);
}

.divPrincipal {
  display: flex;
  justify-content: center;
}

.albumCover {
  margin-top: 100px;
  height: 500px;
  width: 500px;
  display: flex;
  overflow-y: hidden;

  &Brat {
    background-color: $brat-color;
  }

  &Weezer {
    background-color: $weezer-color;
    display: inline-block;
    overflow: hidden;
    transition: all 0.5s ease;
  }
}

.albumTitle {
  color: black;

  &Brat {
    display: flex;
    width: 100%;
    height: 100%;
    font-family: 'brat';
    align-items: center;
    justify-content: center;
    font-size: 10em;
    filter: blur(2px);
    image-rendering: pixelated;
  }

  &Weezer {
    font-family: 'weezered';
    text-align: end;
    margin-right: 25px;
    font-size: 3em;
    transition: all 0.5s ease;
  }

}

.weezerguys {
  display: none;
}

.weezerguys.weezerguys-enabled {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  transition: all 0.5s ease;
}

.weezerimg {
  align-self: end;
  height: 415px;
}

.divButao {
  width: 100%;
  text-align: center;
  padding-top: 16px;
}

#butao {
  width: 300px;
  height: 60px;
  border: 0;
  color: #fff;
  font-size: 1.5em;
  background-color: #333;
}